import React, { useEffect } from "react";
import { useParams } from "react-router";
import { useSelector, useDispatch } from "react-redux";
import { findById, restartInfo } from "../../action";
import "./detail.css";
import Loading from "../loading/loading";
export default function Detail() {
  const { id } = useParams();
  const recipe = useSelector((state) => state.recipesById);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(findById(id));
    return () => {
      dispatch(restartInfo());
    };
  }, [dispatch, id]);
  return Object.keys(recipe).length ? (
    <div className="all">
      <div className="body-detail">
        <div className="body-title-img">
          <h3 className="recipe-title">{recipe.title}</h3>
          <div className="img-detail">
            <img
              src={recipe.image ? recipe.image : null}
              alt="receta"
              className="recipe-image"
            ></img>
            <div className="healtscore-detail">
              HealthScore:{recipe.healthScore}
            </div>
          </div>
          <div className="body-recipe-summary">
            <div
              dangerouslySetInnerHTML={{ __html: recipe.summary }}
              className="recipe-summary"
            ></div>
          </div>
        </div>
        <div className="detail-diet-steps">
          <div className="steps-detail">
            <h3>Steps</h3>
            {recipe.analyzedInstructions &&
              recipe.analyzedInstructions.map((el, i) =>
                el.steps.map((steps) => {
                  return (
                    <div key={steps.step} className="step-box">
                      <span key={steps.number}>
                        {steps.number}-{steps.step}
                      </span>
                    </div>
                  );
                })
              )}
            {recipe.steps &&
              recipe.steps.map((c) => {
                return <div key={c}>{c}</div>;
              })}
          </div>
          <div className="diets-detail">
            <h3>Diets</h3>
            {recipe.diets &&
              recipe.diets.map((c, i) => {
                if (typeof c === "object") {
                  return (
                    <span key={c.name}>
                      {c.name.charAt(0).toUpperCase() + c.name.slice(1)}
                    </span>
                  );
                }
                return (
                  <span key={c}>{c.charAt(0).toUpperCase() + c.slice(1)}</span>
                );
              })}
            <div className="dishtype-detail">
              <h3>Dishtypes</h3>
              {recipe.dishType &&
                recipe.dishType.map((c, i) => {
                  return (
                    <li key={c} className="recipe-dishtype">
                      {c.charAt(0).toUpperCase() + c.slice(1)}
                    </li>
                  );
                })}
            </div>
          </div>
          <div className="diets-detail">
            <h3>Ingredients</h3>
            {recipe.extendedIngredients &&
              recipe.extendedIngredients.map((c) => {
                return (
                  <p key={c.name} className="recipe-diets">
                    {c.name.charAt(0).toUpperCase() + c.name.slice(1)}
                  </p>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  ) : (
    <Loading> </Loading>
  );
}
